<template>

    <div class="users-container">

        <div class="go-back-link">
            <router-link to="/"><span class="icon-arrow-left"></span>На главную</router-link>
        </div>

        <div class="page-title">
            <h1>Пользователи</h1>
        </div>

        <div class="users-table">

            <div class="orders-table-wrapper">

                <div class="orders-table">

                    <div class="orders-table-head">
                        <div class="orders-table-th name">ФИО</div>
                        <div class="orders-table-th position">Должность</div>
                        <div class="orders-table-th phone">Телефон</div>
                        <div class="orders-table-th email">Е-mail</div>
                        <div class="orders-table-th edit"></div>
                    </div>

                    <div class="orders-table-body">

                        <div v-for="user in users"
                             :key="user.id"
                             class="orders-table-tr">

                            <div class="orders-table-tr-flex">

                                <div class="orders-table-td name" data-title="ФИО">{{user.surname}} {{user.name}} {{user.patronymic}}</div>
                                <div class="orders-table-td position" data-title="Должность">{{user.position}}</div>
                                <div class="orders-table-td phone" data-title="Телефон">{{user.phone | VMask('+# ### ###-##-##')}}</div>
                                <div class="orders-table-td email" data-title="Е-mail">{{user.email}}</div>
                                <div class="orders-table-td edit">
                                    <div class="edit-users-btns">
                                        <a v-if="currentUser.access == 1 || currentUser.id == user.id"
                                           href="#"
                                           class="edit-user-link"
                                           @click.prevent="editCurrentUser(user)">
                                            <span class="icon-edit-2"></span>
                                        </a>
                                        <a v-if="currentUser.access == 1 && currentUser.id != user.id"
                                           href="#"
                                           class="edit-user-link delete"
                                           @click="deleteUser(user)">
                                            <span class="icon-trash"></span>
                                        </a>
                                    </div>
                                </div>

                            </div>

                        </div>

                    </div>

                </div>

            </div>

        </div>

        <div v-if="currentUser.access == 1"
             class="users-add-new">

            <button class="btn"
                    @click.prevent="addedUserModal = true">
                Добавить пользователя
            </button>

        </div>

        <Popup v-if="editUserModal"
               ref="editModal"
               @close-modal="closeEditModal()">

            <div class="popup-title">
                <div class="h3">Изменить данные</div>
            </div>

            <div class="popup-body">

                <form class="user-form" @submit.prevent="changeUserData">

                    <fg-input v-model="changedUser.surname"
                              label="Фамилия"
                              :classes="{invalid: $v.changedUser.surname.$error ? !$v.changedUser.surname.required : false }"
                              type="text"/>

                    <fg-input v-model="changedUser.name"
                              label="Имя"
                              :classes="{invalid: $v.changedUser.name.$error ? !$v.changedUser.name.required : false }"
                              type="text"/>

                    <fg-input v-model="changedUser.patronymic"
                              label="Отчество"
                              type="text"/>

                    <fg-input v-model="changedUser.phone"
                              label="Телефон"
                              :mask="'+# ### ### ## ##'"
                              :classes="{invalid: $v.changedUser.phone.$error ? (!$v.changedUser.phone.required || !$v.changedUser.phone.minLength) : false }"
                              type="tel"/>

                    <fg-input v-model="changedUser.email"
                              label="Почта"
                              :classes="{invalid: $v.changedUser.email.$error ? (!$v.changedUser.email.required || !$v.changedUser.email.email) : false }"
                              type="email"/>

                    <fg-input v-model="changedUser.position"
                              label="Должность"
                              :classes="{invalid: $v.changedUser.position.$error ? !$v.changedUser.position.required : false }"
                              type="text"/>

                    <div class="form-submit">
                        <button type="submit"
                                class="btn btn-block btn-lg"
                                :class="{'loading': load}"
                                :disabled="load">
                            Сохранить
                        </button>
                    </div>

                </form>

            </div>

        </Popup>

        <Popup v-if="addedUserModal"
               ref="addedModal"
               @close-modal="closeAddedModal()">

            <div class="popup-title">
                <div class="h3">Добавить пользователя</div>
            </div>

            <div class="popup-body">

                <form class="user-form" @submit.prevent="addUser">

                    <fg-input v-model="addedUser.surname"
                              label="Фамилия"
                              :classes="{invalid: $v.addedUser.surname.$error ? !$v.addedUser.surname.required : false }"
                              type="text"/>

                    <fg-input v-model="addedUser.name"
                              label="Имя"
                              :classes="{invalid: $v.addedUser.name.$error ? !$v.addedUser.name.required : false }"
                              type="text"/>

                    <fg-input v-model="addedUser.patronymic"
                              label="Отчество"
                              type="text"/>

                    <fg-input v-model="addedUser.phone"
                              label="Телефон"
                              :mask="'+# ### ### ## ##'"
                              :classes="{invalid: $v.addedUser.phone.$error ? (!$v.addedUser.phone.required || !$v.addedUser.phone.minLength) : false }"
                              type="tel"/>

                    <fg-input v-model="addedUser.email"
                              label="Почта"
                              :classes="{invalid: $v.addedUser.email.$error ? (!$v.addedUser.email.required || !$v.addedUser.email.email) : false }"
                              type="email"/>

                    <fg-input v-model="addedUser.position"
                              label="Должность"
                              :classes="{invalid: $v.addedUser.position.$error ? !$v.addedUser.position.required : false }"
                              type="text"/>

                    <div class="form-submit">
                        <button type="submit"
                                class="btn btn-block btn-lg"
                                :class="{'loading': load}"
                                :disabled="load">
                            Добавить
                        </button>
                    </div>

                </form>

            </div>

        </Popup>

    </div>

</template>

<script>

    import {required, minLength, email} from 'vuelidate/lib/validators'
    import {mapState} from 'vuex'
    import Popup from '@/components/Blocks/Popup'

    export default {
        name: "Users",
        components: {
            Popup
        },
        validations: {
            changedUser: {
                name: {required},
                surname: {required},
                position: {required},
                phone: {required, minLength: minLength(16)},
                email: {required, email},
            },
            addedUser: {
                name: {required},
                surname: {required},
                position: {required},
                phone: {required, minLength: minLength(16)},
                email: {required, email},
            }
        },
        data: () => ({
            changedUser: {},
            addedUser: {
                name: '',
                surname: '',
                patronymic: '',
                position: '',
                phone: '',
                email: '',
            },
            editUserModal: false,
            addedUserModal: false,
            load: false,
            deletedUser: ''
        }),
        computed: {
          ...mapState({
              currentUser: state => state.users.currentUser,
              users: state => state.users.users
          })
        },
        methods: {
            getUsers() {
                this.$store.dispatch('getUsers').then(() => {
                    this.load = false
                    this.editUserModal ? this.$refs.editModal.closePopup() : null
                    this.addedUserModal ? this.$refs.addedModal.closePopup() : null
                })
            },
            changeUserData() {
                if (this.$v.changedUser.$invalid) {
                    this.$v.changedUser.$touch();
                    return
                }
                this.load = true
                this.changedUser.phone = this.$options.filters.phoneFilter(this.changedUser.phone).replace(/[^0-9]/g, '')
                this.$store.dispatch('updateUserInfo', this.changedUser).then(() => {
                    this.getUsers()
                })
            },
            addUser() {
                if (this.$v.addedUser.$invalid) {
                    this.$v.addedUser.$touch();
                    return
                }
                this.load = true
                this.addedUser.phone = this.$options.filters.phoneFilter(this.addedUser.phone).replace(/[^0-9]/g, '')
                this.$store.dispatch('addUser', this.addedUser).then(() => {
                    this.getUsers()
                })
            },
            closeEditModal() {
                this.editUserModal = false
                this.load = false
            },
            closeAddedModal() {
                this.addedUserModal = false
                this.load = false
                this.addedUser = {
                    name: '',
                    surname: '',
                    patronymic: '',
                    position: '',
                    phone: '',
                    email: '',
                }
            },
            editCurrentUser(user) {
                this.changedUser = Object.assign({}, user)
                this.editUserModal = true
            },
            checkCodeFilled(val, al) {
                if ( val == true ) {
                    this.codeVal = al
                    this.sendCodeDisabled = false
                }
                else {
                    this.sendCodeDisabled = true
                }
            },
            deleteUser(user) {
                this.deletedUser = user
                this.$store.dispatch('deleteUser', this.deletedUser.id).then(() => {
                    this.closeCodeModalSuccess()
                    this.getUsers()
                })
            },
            closeCodeModalSuccess() {
                this.getUsers()
            }
        },
        mounted() {
            this.getUsers()
        }
    }
</script>
